<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <SearchBox @query="onQueryChange" placeholder="请输入日期/关键词"></SearchBox>
        </div>
      </div>
      <div class="goods-wrapper">
        <ul class="tab" ref="tab">
          <li class="tab-item active" @click="selectTab('0')"><span>全部</span></li>
          <li class="tab-item" @click="selectTab('1')"><span>待签收</span></li>
          <li class="tab-item" @click="selectTab('2')"><span>已完成</span></li>
        </ul>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box'
  import {addClass,removeClass} from 'common/js/dom'

  export default {
    data () {
      return {
        query: '',
        status: '0'//0,1,2
      }
    },
    created () {
      this._getOrderList()
    },
    methods: {
      back() {
        this.$router.push({
          'path': '/purchase'
        })
      },
      _getOrderList() {
        // this.getDistributeOrderList(this.status)
      },
      showCategory() {

      },
      onQueryChange(newQuery) {
        this.query = newQuery
        //dosomething
      },
      selectTab(status){
        this.status = status
        let tab = this.$refs.tab
        let children = tab.children
        for(let i = 0; i < children.length; i++) {
          let child = children[i]
          if(status == i) {
            addClass(child, 'active')
          } else {
            removeClass(child, 'active')
          }
        }
      }
    },
    watch: {
      status(){
        this._getOrderList()
      }
    },
    components: {
      SearchBox
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
        margin-right 33px
    .goods-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      flex-direction column
      .tab
        display flex
        height 45px
        border-top 1px solid #E6E6E6
        background #fff
        color #4c4c4c
        font-size $font-size-medium-m
        width 100%
        .tab-item
          display flex
          flex 1
          justify-content center
          height 100%
          span
            display flex
            height 100%
            flex-direction column
            justify-content center
          &.active
            span
              color #eb4747
              position relative
              &:after
                position absolute
                height 2px
                background #eb4747
                bottom 0
                left 0
                right 0
                content ''




</style>



